@font-face {
  font-style: normal;
  font-weight: 300;
  font-family: "Roboto Condensed";
  font-display: swap;
  src: url("./../fonts/robotocondensed-normal-300.woff2") format("woff2");
}
@font-face {
  font-style: normal;
  font-weight: 400;
  font-family: "Roboto Condensed";
  font-display: swap;
  src: url("./../fonts/robotocondensed-normal-400.woff2") format("woff2");
}
@font-face {
  font-style: normal;
  font-weight: 700;
  font-family: "Roboto Condensed";
  font-display: swap;
  src: url("./../fonts/robotocondensed-normal-700.woff2") format("woff2");
}
@font-face {
  font-style: normal;
  font-weight: 600;
  font-family: "Alumni Sans";
  font-display: swap;
  src: url("./../fonts/alumnisans-normal-600.woff2") format("woff2");
}
@font-face {
  font-style: normal;
  font-weight: 700;
  font-family: "Alumni Sans";
  font-display: swap;
  src: url("./../fonts/alumnisans-normal-700.woff2") format("woff2");
}
@font-face {
  font-style: normal;
  font-weight: 800;
  font-family: "Alumni Sans";
  font-display: swap;
  src: url("./../fonts/alumnisans-normal-800.woff2") format("woff2");
}
*,
*::before,
*::after {
  box-sizing: border-box;
}

html,
body {
  margin: 0;
  padding: 0;
  min-height: 100vh;
}

html {
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 24px;
  font-family: "Placeholder", "Arial", sans-serif;
  color: #000000;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-rendering: optimizeLegibility;
}

body {
  width: 100%;
  height: 100%;
  background-color: #ffffff;
}

a {
  color: #000000;
  text-decoration: none;
}

img,
video {
  display: block;
  max-width: 100%;
  height: auto;
}

textarea {
  resize: none;
}

input:-webkit-autofill {
  box-shadow: inset 0 0 0 1000px #ffffff;
  -webkit-text-fill-color: #000000;
}

input,
textarea {
  border-radius: 0;
}
input::placeholder,
textarea::placeholder {
  opacity: 1;
}
input:invalid,
textarea:invalid {
  box-shadow: none;
}

select {
  border-radius: 0;
}

input::-ms-clear, input::-ms-reveal {
  display: none;
}

input[type=search]::-webkit-search-decoration,
input[type=search]::-webkit-search-cancel-button,
input[type=search]::-webkit-search-results-button,
input[type=search]::-webkit-search-results-decoration {
  appearance: none;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  margin: 0;
  appearance: none;
}

input[type=number] {
  appearance: textfield;
}

select,
textarea,
input:matches([type=email],
[type=number],
[type=password],
[type=search],
[type=tel],
[type=text],
[type=url]) {
  appearance: none;
}

button,
[type=button],
[type=reset],
[type=submit] {
  appearance: none;
}

.wrapper {
  position: relative;
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  /* stylelint-disable-next-line */
  min-height: calc(100 * var(--vh, 1vh));
}
.wrapper .header,
.wrapper .footer {
  flex-shrink: 0;
}
.wrapper main {
  flex-grow: 1;
}

.rouble {
  font-family: "Rouble", "Arial", sans-serif;
  /* stylelint-disable */
  text-transform: lowercase !important;
  font-weight: 400 !important;
  /* stylelint-enable */
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  white-space: nowrap;
  border: 0;
  clip: rect(0 0 0 0);
  clip-path: inset(100%);
}

.scroll-lock-ios {
  position: fixed;
  overflow: hidden;
}

.scroll-lock {
  overflow: hidden;
}

.no-scrollbar {
  -ms-overflow-style: none;
  scrollbar-width: none;
}
.no-scrollbar::-webkit-scrollbar {
  display: none;
}

.no-transition {
  /* stylelint-disable-next-line */
  transition: none !important;
}

.js-focus-visible :focus:not(.focus-visible) {
  outline: none;
}

.container {
  width: 100%;
  max-width: 1366px;
  margin: 0 auto;
  padding: 0 103px;
}
@media (max-width: 1199px) {
  .container {
    padding: 0 45px;
  }
}
@media (max-width: 767px) {
  .container {
    padding: 0 15px;
  }
}

.btn {
  display: inline-flex;
  justify-content: center;
  padding: 24px 56px;
  margin: 0;
  color: #ffffff;
  border: 4px solid;
  cursor: pointer;
  font-family: "Roboto Condensed";
  font-size: 12px;
  font-weight: 700;
  letter-spacing: 0.6px;
  text-transform: uppercase;
}
@media (max-width: 1199px) {
  .btn {
    padding: 24px 33px;
  }
}

.page {
  display: grid;
  grid-template-rows: min-content 1fr min-content;
  min-height: 100vh;
  overflow-x: hidden;
}

.page-header {
  padding: 30px 0 0;
  color: #ffffff;
  background: conic-gradient(from 130.31deg at 16.03% 39.06%, #b80027 -46.69deg, #003d85 32.24deg, #b80027 313.31deg, #003d85 392.24deg);
}
@media (max-width: 1199px) {
  .page-header {
    padding-top: 34px;
    background: conic-gradient(from 139.4deg at 20.7% 35.74%, #b80027 -46.69deg, #003d85 32.24deg, #b80027 313.31deg, #003d85 392.24deg);
  }
}
@media (max-width: 767px) {
  .page-header {
    padding-top: 16px;
    padding-bottom: 81px;
    background: conic-gradient(from 171.43deg at 35.31% 34.22%, #b80027 -46.69deg, #003d85 32.24deg, #b80027 313.31deg, #003d85 392.24deg);
  }
}

.page-header__container {
  position: relative;
  display: flex;
  justify-content: space-between;
  gap: 30px;
  font-weight: 600;
  font-size: 26px;
  line-height: 31px;
  letter-spacing: 0.03em;
}
@media (max-width: 1199px) {
  .page-header__container {
    font-weight: 700;
    font-size: 24px;
    line-height: 29px;
    letter-spacing: 0.02em;
  }
}
@media (max-width: 767px) {
  .page-header__container {
    margin-bottom: 73px;
    font-weight: 600;
    font-size: 20px;
    line-height: 24px;
  }
}

.page-header__logo {
  margin-top: 10px;
}
@media (max-width: 1199px) {
  .page-header__logo {
    width: 82px;
    margin-top: 7px;
  }
}
@media (max-width: 767px) {
  .page-header__logo {
    width: 69px;
    margin-top: 5px;
  }
}

.page-header__link {
  position: relative;
  display: inline-block;
  font-family: "Alumni Sans";
  font-weight: 600;
  font-size: 26px;
  line-height: 20px;
  letter-spacing: 0.75px;
  color: #ffffff;
}
@media (max-width: 1199px) {
  .page-header__link {
    font-weight: 700;
    font-size: 24px;
    letter-spacing: 0.48px;
  }
}
@media (max-width: 767px) {
  .page-header__link {
    font-weight: 600;
    font-size: 20px;
    letter-spacing: 0.34px;
  }
}

.page-header__teaser {
  margin-top: -63px;
}
@media (max-width: 1199px) {
  .page-header__teaser {
    margin-top: 0;
  }
}

.teaser {
  display: grid;
  grid-template-rows: 1fr min-content min-content;
  grid-template-columns: 133px 246px 1fr;
  grid-template-areas: "features label image" "features heading image" "features btn image";
  gap: 13px 16px;
  min-height: 658px;
  padding-top: 51px;
  padding-bottom: 51px;
}
@media (max-width: 1199px) {
  .teaser {
    grid-template-columns: 113px 187px 1fr;
    gap: 12px 24px;
    min-height: auto;
    padding-top: 19px;
    padding-bottom: 82px;
  }
}
@media (max-width: 767px) {
  .teaser {
    grid-template-areas: ". label" "features heading" "btn btn";
    grid-template-columns: 113px 1fr;
    column-gap: 12px;
    padding-top: 0;
    padding-bottom: 0;
  }
}

.teaser__label,
.teaser__heading {
  font-weight: 600;
  font-family: "Alumni Sans";
}

.teaser__label {
  grid-area: label;
  align-self: end;
  margin: 0 0 0 64px;
  font-size: 48px;
  line-height: 58px;
  letter-spacing: 0.03em;
  text-align: center;
  text-transform: lowercase;
}
@media (max-width: 1199px) {
  .teaser__label {
    margin: 0 4px -5px 0;
    font-size: 34px;
    line-height: 40.8px;
    text-align: right;
    letter-spacing: 1.02px;
  }
}
@media (max-width: 767px) {
  .teaser__label {
    margin: -6px 3px 2px 0;
    font-size: 28px;
    line-height: 34px;
  }
}

.teaser__heading {
  grid-area: heading;
  margin: 0 0 39px;
  font-size: 92px;
  line-height: 110px;
  letter-spacing: 0.05em;
  -webkit-text-stroke: 4px;
  text-align: right;
  text-transform: uppercase;
  overflow-wrap: normal;
}
@media (max-width: 1199px) {
  .teaser__heading {
    margin-bottom: 27px;
    font-size: 72px;
    line-height: 86px;
    text-transform: none;
    text-align: right;
    letter-spacing: 3.6px;
  }
}
@media (max-width: 767px) {
  .teaser__heading {
    margin-left: -15px;
  }
}

.teaser__features {
  grid-area: features;
  align-self: center;
  margin: 0;
  padding: 117px 0 0;
  font-family: "Roboto Condensed";
  font-weight: 400;
  line-height: 21px;
  list-style: none;
  font-size: 14px;
  text-align: right;
}
@media (max-width: 1199px) {
  .teaser__features {
    padding-top: 44px;
  }
}
@media (max-width: 767px) {
  .teaser__features {
    align-self: start;
    padding: 78px 0 30px;
  }
}

.teaser__btn {
  grid-area: btn;
  margin: 0 0 45px 16px;
  outline: 2px solid;
}
@media (max-width: 1199px) {
  .teaser__btn {
    margin-bottom: 25px;
    margin-left: 4px;
  }
}
@media (max-width: 767px) {
  .teaser__btn {
    margin-bottom: 0;
    margin-left: 0;
  }
}

.teaser__image {
  grid-area: image;
  margin: 0 0 0 49px;
}
@media (max-width: 1199px) {
  .teaser__image {
    margin-left: 6px;
  }
}
@media (max-width: 767px) {
  .teaser__image {
    display: none;
  }
}
.teaser__image img {
  object-fit: cover;
}

.page-footer {
  position: relative;
  padding: 40px 0;
  color: #ffffff;
  background-color: #000000;
}
@media (max-width: 1199px) {
  .page-footer {
    padding: 44px 0;
  }
}

.page-footer__inner {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
@media (max-width: 1199px) {
  .page-footer__inner {
    padding-right: 39px;
  }
}
@media (max-width: 767px) {
  .page-footer__inner {
    gap: 34px;
    flex-wrap: wrap;
  }
}

.page-footer__logo {
  flex-shrink: 0;
  width: 86px;
  margin-top: 11px;
}
@media (max-width: 1199px) {
  .page-footer__logo {
    width: 82px;
    margin-top: 0;
  }
}
@media (max-width: 767px) {
  .page-footer__logo {
    position: absolute;
    top: 51px;
    left: 15px;
    width: 68px;
  }
}

@media (max-width: 767px) {
  .page-footer__nav-wrapper {
    width: 100%;
  }
}

.page-footer__nav {
  justify-content: center;
  margin: 0 0 0 11px;
}
@media (max-width: 767px) {
  .page-footer__nav {
    display: block;
    margin: 0;
    column-count: 2;
  }
  .page-footer__nav li {
    margin-top: 16px;
  }
  .page-footer__nav li:first-child {
    margin-top: 41px;
  }
}

.page-footer__socials {
  justify-content: flex-end;
  min-width: 100px;
  margin: 0;
}
@media (max-width: 767px) {
  .page-footer__socials {
    margin-left: -3px;
  }
}

.nav {
  display: flex;
  flex-wrap: wrap;
  gap: 10px 41px;
  padding: 0;
  list-style: none;
}
@media (max-width: 1199px) {
  .nav {
    column-gap: 30px;
  }
}
@media (max-width: 767px) {
  .nav {
    display: block;
  }
}

.nav__link {
  font-size: 14px;
  line-height: 16px;
  color: #ffffff;
}
@media (max-width: 767px) {
  .nav__link {
    font-size: 16px;
    line-height: 19px;
  }
}

.socials {
  padding: 0;
  list-style-type: none;
  text-transform: uppercase;
  display: grid;
  gap: 11px;
  grid-template-columns: 25px 25px 25px;
}

.socials__item {
  width: 25px;
  height: 25px;
  border-radius: 50%;
  background-color: #ffffff;
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  align-items: center;
}
.socials__item:hover {
  background-color: #669ec0;
}
.socials__item:active {
  background-color: #5496bd;
}

.socials__link {
  display: flex;
  justify-content: center;
  align-items: center;
}

.social__icon {
  fill: #ffffff;
}

.social__item:active .social__icon {
  fill: #87b5d1;
}

